import { Component, OnInit } from '@angular/core';
import { ClienteService } from './cliente.service';
import { Cliente } from './cliente';
import { Observable } from 'rxjs';
import swal from 'sweetalert2';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-cliente',
  templateUrl: './cliente.component.html',
  styleUrls: ['./cliente.component.css']
})
export class ClienteComponent implements OnInit {

  clientes: Cliente[];
  paginador: any;

  constructor(private clienteService: ClienteService, private activeteRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.activeteRoute.paramMap.subscribe(params => {
      let pages = +params.get('page');
      if (!pages) {
        pages = 0;
      }
      this.clienteService.getClientes(pages).subscribe(
        response => {
          this.clientes = response.content as Cliente[];
          this.paginador = response;
        });
    });
  }
  // ngOnInit(): void {
  //   this.clienteService.getClientes(0).subscribe({
  //     next: response => this.clientes = response.content as Cliente[]
  //   });
  // }

  delete(cliente: Cliente): void {
    swal.fire({
      title: 'Are you sure?',
      text: 'You wont be able to revert this!',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, cancel!',
      reverseButtons: true
    }).then((result) => {
      if (result.value) {
        this.clienteService.delete(cliente.idCliente).subscribe(
          response => {
            this.clientes = this.clientes.filter(cli => cli !== cliente);
            swal.fire(
              'Deleted!',
              'Your file has been deleted.',
              'success'
            );
          }
        );
      }
    });
  }
}
