import { Component, OnInit } from '@angular/core';
import { Cliente } from './cliente';
import { ClienteService } from './cliente.service';
import { Router, ActivatedRoute } from '@angular/router';
import swal from 'sweetalert2';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html'
})
export class FormComponent implements OnInit {

  public cliente: Cliente = new Cliente();
  public errores: string[];

  constructor(private clienteService: ClienteService, private router: Router, private activateRouter: ActivatedRoute) { }

  ngOnInit(): void {
    this.cargarCliente();
  }

  cargarCliente(): void {
    this.activateRouter.params.subscribe(params => {
      const id = params.id;
      if (id) {
        return this.clienteService.getCliente(id).subscribe({
          next: (cliente) => this.cliente = cliente
        });
      }
    });
  }

  create(): void {
    this.clienteService.create(this.cliente).subscribe(
      cliente => {
        this.router.navigate(['/clientes']);
        swal.fire('Nuevo Cliente', `El cliente ${cliente.nombreCliente} ha sido creado con exito!!!`, 'success');
      },
      err => {
        this.errores = err.error.errors as string[];
        console.error(this.errores);
      }
    );
  }

  update(): void {
    this.clienteService.update(this.cliente).subscribe(
      cliente => {
        this.router.navigate(['/clientes']);
        swal.fire('Cliente Actualizado', `Cliente ${cliente.nombreCliente} actualizado con exito!`, 'success');
      }
    );
  }
}
