import { Injectable } from '@angular/core';
import { DatePipe } from '@angular/common';
import { Cliente } from './cliente';
import { Observable, of, throwError } from 'rxjs';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Router  } from '@angular/router';
import { catchError, map } from 'rxjs/operators';
import swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class ClienteService {

  private urlEndPoint = 'http://192.168.1.8:8080/api/clientes';
  private httpHeaders = new HttpHeaders({'Content-Type': 'application/json'});

  constructor(private http: HttpClient, private router: Router) { }

  getClientes(page: number): Observable<any> {
    return this.http.get(`${this.urlEndPoint}/page/${page}`).pipe(
      map( (response: any) => {
        (response.content as Cliente[]).map(
          cliente => {
            let datePipe = null;
            datePipe = new DatePipe('es');
            cliente.createAt = datePipe.transform(cliente.createAt, 'EEE dd, MMMM yyy');
            cliente.fechaNacimientoCliente = datePipe.transform(cliente.fechaNacimientoCliente, 'EEE dd, MMMM yyy');
            return cliente;
          }
        );
        return response;
      })
    );
  }

  // getClientes(): Observable<Cliente[]> {
  //     return this.http.get(this.urlEndPoint).pipe(
  //       map( response => {
  //         let clientes = null;
  //         clientes = response as Cliente[];
  //         return clientes.map( cliente => {
  //           cliente.nombreCliente = cliente.nombreCliente.toUpperCase();
  //           let datePipe = null;
  //           datePipe = new DatePipe('es');
  //           cliente.createAt = datePipe.transform(cliente.createAt, 'EEE dd, MMMM yyy');
  //           return cliente;
  //         });
  //       })
  //     );
  // }

  getCliente(id): Observable<Cliente> {
    return this.http.get<Cliente>(`${this.urlEndPoint}/${id}`).pipe(
      catchError(e => {
        this.router.navigate(['/clientes']);
        console.error(e.error.mensaje);
        swal.fire('Error al Editar', e.error.mensaje, 'error');
        return throwError(e);
      })
    );
  }

  create(cliente: Cliente): Observable<Cliente> {
    return this.http.post(this.urlEndPoint, cliente, {headers: this.httpHeaders}).pipe(
      map((response: any) => response.cliente),
      catchError(e => {
        if ( e.status === 400) {
          return throwError(e);
        }
        console.error(e.error.mensaje);
        swal.fire(e.error.mensaje, e.error.error, 'error');
        return throwError(e);
      })
    );
  }

  // create(cliente: Cliente): Observable<any> {
  //   return this.http.post<any>(this.urlEndPoint, cliente, {headers: this.httpHeaders}).pipe(
  //     catchError(e => {
  //       console.error(e.error.mensaje);
  //       swal.fire(e.error.mensaje, e.error.error, 'error');
  //       return throwError(e);
  //     })
  //   );
  // }

  update( cliente: Cliente ): Observable<Cliente> {
    return this.http.put(`${this.urlEndPoint}/${cliente.idCliente}`, cliente, {headers: this.httpHeaders}).pipe(
      map((response: any) => response.cliente),
      catchError(e => {
        console.error(e.error.mensaje);
        swal.fire(`Error al Actualizar Cliente ${cliente.nombreCliente}`, e.error.mensaje, 'error');
        return throwError(e);
      })
    );
  }

  // update( cliente: Cliente ): Observable<any> {
  //   return this.http.put<any>(`${this.urlEndPoint}/${cliente.idCliente}`, cliente, {headers: this.httpHeaders}).pipe(
  //     catchError(e => {
  //       console.error(e.error.mensaje);
  //       swal.fire(`Error al Actualizar Cliente ${cliente.nombreCliente}`, e.error.mensaje, 'error');
  //       return throwError(e);
  //     })
  //   );
  // }

  delete(id: number): Observable<Cliente> {
    return this.http.put<Cliente>(`${this.urlEndPoint}/dl/${id}`, {headers: this.httpHeaders}).pipe(
      catchError(e => {
        console.error(e.error.mensaje);
        swal.fire(`Error al Eliminar el Cliente con el ${id}`, e.error.mensaje, 'error');
        return throwError(e);
      })
    );
  }
}
